            <div :class="{'mainItemTitleWrapper': isLevelOne(item.title),'subItemTitleWrapper': !isLevelOne(item.title)}"
                 @click="$store.commit('resumeModule/setOpen', item.id)"
            >
                <h2 class="itemTitle"
                    :style="{fontSize:item.title==='Портфолио'&&'23px', fontWeight:item.title==='Портфолио'&&600}"
