                        :href="item.githubLink"
                        target='_blank' rel="noopener noreferrer"
                        v-if="item.done && item.githubLink"
                >
                    Домашняя работа
                </a>
                <div class="icon iconDone" v-if="item.done"/>
                <div class="icon iconPause" v-if="item.inProcess"/>
                <div class="icon iconGithub" v-if="item.githubLink"/>
