                    <span v-else>{{item.title}}</span>
                </h3>
                <a v-if="item.githubLink" class="icon iconGithub" :href="item.githubLink" target="_blank" rel="noopener noreferrer"/>
                <a v-if="item.websiteLink" class="icon iconWebsite" :href="item.websiteLink" target="_blank" rel="noopener noreferrer"/>
