    </div>
</template>

<script>
  import DropItem from "../../components/Resume/DropItem";
  export default {
    name: 'DropItem',
    components: {DropItem},
    props:['item'],
    methods: {
      isLevelOne(titleStr) {
        const filteredItem = this.levelOneArr.filter(itemTitle => titleStr === itemTitle);
        return filteredItem[0] === titleStr;
      }
    },
    computed:{
      levelOneArr(){
        return this.$store.state.resumeModule.droplist.map(item=>item.title) ||[];
      }
    },
  }
</script>

